.costs-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 60px;
  text-align: left;
  vertical-align: middle;
  border: 1px solid $border;
  @include e(element) {
    padding: 20px 0 20px 20px;
    border-bottom: 1px solid $border;
    margin: 0;
    @include font('DM Sans', 19px, $regularFontWeight, $tags);
    @include m(head) {
      display: contents;
    }
    @include m(head-title-product) {
      grid-column: 1;
    }
    @include m(quantity) {
      font-weight: 500;
    }
    @include m(price-total) {
      font-weight: 600;
    }
    @include m(head-title) {
      background: #111111;
      @include font('DM Sans', 19px, $mediumFontWeight, $white);
      padding: 20px 0 20px 0;
      text-align: center;
      margin-top: 0;
      margin-bottom: 0;
    }
    @include m(order) {
      display: contents;
    }
    @include m(val-box) {
      display: contents;
    }
    @include m(price) {
      text-align: center;
    }
  }
}
.order-box {
  @include e(element) {
    padding-left: 20px;
    text-align: left;
  }
}
