.menu {
  max-width: 400px;
  margin: auto;
  height: 100vh;
  position: fixed;
  z-index: 99999;
  top: 3px;
  background: $white;
  text-align: left;
  left: 0;
  overflow: scroll;
  &::-webkit-scrollbar {
    width: 0;
  }
  @include e(link) {
    text-decoration: none;
    @include font('DM Sans', 14px, $mediumFontWeight, $navElement);
  }
  @include e(img) {
    max-height: 36px;
  }
  @include e(items) {
    padding-left: 0;
  }
  @include e(item) {
    list-style: none;
    text-transform: uppercase;
    line-height: 1.5rem;
    padding: 10px 0;
    border-bottom: 1px solid #e8e8e8;
    &:last-of-type {
      border-bottom: none;
    }
  }
  display: none;
}
.menu-wrap {
  // padding-top: 50px;
  max-width: 80%;
  margin: auto;
  height: 100%;
  display: grid;
  grid-template-rows: auto auto 1fr;
  position: relative;
}
.menu-button {
  display: inline-block;
  transition: 0.3s ease-in-out;
  width: 25px;
  height: 15px;
  position: relative;
}
.menu-button.in-mobile {
  position: absolute;
  right: 0px;
}

.menu-button span {
  position: absolute;
  height: 2px;
  border-radius: 10px;
  width: 100%;
  background: $navElement;
  top: 0;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}

.menu-button span:nth-child(1) {
  top: 0;
}

.menu-button span:nth-child(2),
.menu-button span:nth-child(3) {
  top: 8px;
}

.menu-button span:nth-child(4) {
  top: 16px;
}

.menu-button.active span:nth-child(1),
.menu-button.active span:nth-child(4) {
  opacity: 0;
}

.menu-button.active span:nth-child(2) {
  transform: rotate(45deg);
}

.menu-button.active span:nth-child(3) {
  transform: rotate(-45deg);
}

.mark {
  @include m(menu) {
    background: transparent;
    padding: 0;
    width: 100%;
    text-align: center;
    margin: 0;
    align-self: end;
  }
}
.text__title {
  @include m(menu) {
    margin-bottom: 10px;
    margin-top: 0;
  }
}
.form__input {
  @include m(menu) {
    max-width: 100%;
  }
}
.form__btn {
  @include m(menu) {
    max-width: 100%;
  }
}
.wrap {
  margin-top: 50px;
}
.dark-overlay {
  position: fixed;
  top: 0;
  left: 0;
  visibility: hidden;
  z-index: 99998;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
}

.dark-overlay.visible {
  opacity: 1;
  visibility: visible;
}
