.slider {
  position: relative;
  @include e(item) {
    height: 900px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 100px;
  }
}
.slick-arrow {
  height: 35px;
  width: 35px;
  border: none;
  background: $white;
  position: absolute;
  z-index: 3;
  top: 35%;
  border-radius: 50%;
  font-size: 0;
  background-size: 10px 10px;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url('../i/arrow.svg');
  cursor: pointer;
  &.slick-prev {
    left: 10px;
    transform: rotate(90deg);
  }
  &.slick-next {
    right: 10px;
    transform: rotate(270deg);
  }
}

