.card {
  max-width: 700px;
  background: $bg;
  display: flex;
  flex-direction: column;
  margin: auto;
  @include e(title) {
    order: 1;
    width: 100%;
    @include font('DM Sans', 14px, $mediumFontWeight);
    letter-spacing: 0.2em;
    text-transform: uppercase;
  }
}
.mark {
  margin: auto;
  background: $bg;
  padding: 35px 25px;
  margin-top: 40px;
  margin-bottom: 50px;
  width: calc(100% - 50px);
}
.text {
  @include e(title) {
    @include font('DM Sans', 30px, $regularFontWeight);
  }
  @include e(description) {
    @include font('Source Serif Pro', 18px, $regularFontWeight);
  }
}
.form {
  @include e(input) {
    @include font('Source Serif Pro', 18px, $regularFontWeight, $tags);
    line-height: 1.6em;
    width: calc(100% - 20px);
    max-width: 250px;
    padding: 14px 8px;
    margin: 10px 0;
  }
  @include e(btn) {
    @include font('DM Sans', 13px, $mediumFontWeight, $white);
    line-height: 1.6em;
    background: $green;
    text-transform: uppercase;
    width: 100%;
    max-width: 250px;
    padding: 10px 0;
    margin: 10px 0;
    border: none;
    border-radius: 3px;
    letter-spacing: 0.1em;
    line-height: 2.5rem;
  }
}
.cart-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid $border;
  // &:last-of-type {
  //   border-bottom: none;
  // }
  @include e(element) {
    @include m(title) {
      @include font('DM Sans', 17px, $regularFontWeight);
      margin-bottom: 0;
      text-align: left;
    }
    @include m(sub) {
      @include font('DM Sans', 12px, $regularFontWeight, $grey);
      display: flex;
    }
    @include m(img) {
      max-width: 62px;
    }
    @include m(description) {
      width: 180px;
      padding-left: 10px;
      box-sizing: border-box;
    }
    @include m(cross) {
      margin-left: 10px;
      margin-right: 10px;
    }
    @include m(remove) {
      border: none;
      background: transparent;
      padding-left: 0;
    }
  }
}
.cart-total {
  display: flex;
  justify-content: start;
  @include e(title) {
    @include font('DM Sans', 12px, $regularFontWeight, $tags);
  }
  @include e(price) {
    @include font('DM Sans', 12px, $regularFontWeight, $tags);
  }
}
.cart-buttons {
  @include e(btn) {
    @include font('DM Sans', 11px, $mediumFontWeight, $white);
    background: $green;
    border: none;
    border-radius: 3px;
    padding: 13px 31px;
    text-transform: uppercase;
  }
}

.container {
  max-width: 90%;
  margin: auto;
}
.small-container {
  // max-width: 90%;
  margin: auto;
  max-width: 1200px;
}
.wrapper {
  display: grid;
}
.upBtn {
  background: $green;
  border-radius: 50%;
  height: 45px;
  width: 45px;
  line-height: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 99997;
  cursor: pointer;
}
.upBtn_hide {
  display: none;
}
.menu.visible {
  display: block;
}
textarea {
  resize: vertical;
}
