.first-block {
  order: 1;
  @include e(copyright) {
    @include font('DM Sans', 14px, $regularFontWeight, $tags);
    margin-top: 15px;
  }
}
.second-block {
  @include e(img) {
    max-height: 50px;
  }
}
.footer {
  display: flex;
  flex-direction: column;
  margin: 60px auto;
  width: 90%;
}
.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  @include e(item) {
    margin: 0 10px;
    margin-top: 10px;
  }
}
.social {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
  @include e(item) {
    text-decoration: none;
  }
}
