.cart-buttons {
  margin-bottom: 10px;
}
.cost-box__total {
  display: grid;
  grid-column: 1 / -1;
  @include m(span) {
    grid-column: 1 / span 2;
  }
  @include m(price) {
    grid-column: 3 / -1;
  }
}
.cost-box__subtotal {
  display: grid;
  grid-column: 1 / -1;
  @include m(span) {
    grid-column: 1 / span 2;
  }
  @include m(price) {
    grid-column: 3 / -1;
  }
}
