.cart-item {
  text-align: center;
  border-bottom: 1px solid $border;
  &:last-of-type {
    border-bottom: none;
  }
  grid-template-columns: 0.5fr 1fr 1fr;
  grid-column-gap: 2px;
  @include m(page) {
    display: contents;
    margin: 20px 0 20px 0;
    border-bottom: 1px solid $grey;
  }

  @include e(element) {
    justify-self: center;
    @include m(remove) {
      padding-left: 10px;
    }
    @include m(remove-page) {
      cursor: pointer;
      padding: 0 0 0 18px;
      grid-column: 1;
    }
    @include m(img-box-page) {
      grid-column: 2;
    }
    @include m(img-box) {
      padding-top: 10px;
    }
    @include m(title-page) {
      grid-column: 3;
      text-align: left;
    }
    @include m(price-page) {
      grid-column: 4;
    }
    @include m(page) {
      margin: 10px 0;
    }
  }
}
.cart-page {
  @include e(title) {
    @include font('DM Sans', 48px, $regularFontWeight);
    margin-bottom: 0;
    line-height: 1.2em;
  }
}
.cost-box {
  border: 1px solid $black;
  display: contents;
}
