.post {
  @include m(search) {
    list-style: none;
    display: flex;
    flex-direction: row;
    // padding-left: 20px;
    box-sizing: border-box;
    padding-top: 10px;
    &:last-of-type {
      padding-bottom: 10px;
    }
  }
  @include e(img) {
    @include m(search) {
      width: 50px;
      height: 50px;
      left: 3%;
      border-radius: 10px;
    }
  }
  @include e(img-box) {
    @include m(search) {
      width: 65px;
      height: 65px;
      padding-bottom: 0;
    }
  }
  @include e(title) {
    @include m(search) {
      text-align: left;
      order: 1;
      margin-top: 0;
      margin-left: 10%;
      max-width: 60%;
    }
    @include m(user-value) {
      margin-left: 1%;
    }
  }
  @include e(tag) {
    @include m(search) {
      right: 3%;
      position: absolute;
    }
    @include m(post) {
      margin-right: 13px;
    }
  }
}

.search {
  position: relative;
  cursor: pointer;
}
.search-form {
  position: absolute;
  width: 100%;
  left: 0;
  top: 20px;
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  background: $white;
  transition: visiblity 0.3s, opacity 0.3s;
  &.active {
    visibility: visible;
    opacity: 1;
    transition: visiblity 0.3s, opacity 0.3s;
  }
  @include e(results-wrap) {
    border: 1px solid $grey;
    border-radius: 20px;
    width: 90%;
    margin-right: auto;
    margin-left: auto;
  }
  @include e(input) {
    width: 90%;
    left: 0;
    padding: 15px 20px;
    box-sizing: content-box;
    position: relative;
    border-radius: 25px;
    @include font('DM Sans', 16px, $regularFontWeight);
  }
  @include e(span) {
    position: absolute;
    cursor: pointer;
    right: 3%;
    top: 15px;
  }
}

input[type='text']::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}
input[type='text']::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  display: none;
}
