//   margin-top: 5px;
// }
.coupon {
  grid-area: coupon;
}
.cart-table {
  grid-area: cart;
  @include e(btn) {
    padding: 12px;
  }
  @include e(btn-box) {
    grid-area: cart;
  }
}
.sidebar {
  @include m(cart) {
    grid-area: aside;
  }
}
